import React, { ChangeEvent } from 'react';
import { InlineField, Input } from '@grafana/ui';
import { QueryEditorProps } from '@grafana/data';
import { DataSource } from '../datasource';
import { StreamingDataSourceOptions, StreamingQuery } from '../types';

type Props = QueryEditorProps<DataSource, StreamingQuery, StreamingDataSourceOptions>;

export function QueryEditor({ query, onChange, onRunQuery }: Props) {
  const onTopicChange = (event: ChangeEvent<HTMLInputElement>) => {
    onChange({ ...query, topic: event.target.value });
  };

  const { topic } = query;

  return (
    <div className="gf-form">
      <InlineField label="Topic Name" labelWidth={20} >
        <Input onChange={onTopicChange} value={topic || ''} />
      </InlineField>
    </div>
  );
}
