import React, { ChangeEvent } from 'react';
import { InlineField, Input } from '@grafana/ui';
import { DataSourcePluginOptionsEditorProps } from '@grafana/data';
import { StreamingDataSourceOptions } from '../types';

interface Props extends DataSourcePluginOptionsEditorProps<StreamingDataSourceOptions> {}

export function ConfigEditor(props: Props) {
  const { onOptionsChange, options } = props;
  const onKafkaUrlChange = (event: ChangeEvent<HTMLInputElement>) => {
    const jsonData = {
      ...options.jsonData,
      kafkaUrl: event.target.value,
    };
    onOptionsChange({ ...options, jsonData });
  };
  const onSchemaRegistryUrlChange = (event: ChangeEvent<HTMLInputElement>) => {
    const jsonData = {
      ...options.jsonData,
      schemaRegistryUrl: event.target.value,
    };
    onOptionsChange({ ...options, jsonData });
  };

  const { jsonData } = options;


  return (
    <div className="gf-form-group">
      <InlineField label="Kafka URL" labelWidth={20}>
        <Input
          onChange={onKafkaUrlChange}
          value={jsonData.kafkaUrl || ''}
          placeholder="< kafka:29092 >"          
          width={40}
        />
      </InlineField>
      <InlineField label="Schema Registry URL" labelWidth={20}>
        <Input
          onChange={onSchemaRegistryUrlChange}
          value={jsonData.schemaRegistryUrl || ''}          
          placeholder=" < http://schema-registry:8081 >"
          width={40}
        />
      </InlineField>
    </div>
  );
}
