import { DataSourceInstanceSettings, CoreApp } from '@grafana/data';
import { DataSourceWithBackend } from '@grafana/runtime';

import { StreamingQuery, StreamingDataSourceOptions, DEFAULT_QUERY } from './types';

export class DataSource extends DataSourceWithBackend<StreamingQuery, StreamingDataSourceOptions> {
  constructor(instanceSettings: DataSourceInstanceSettings<StreamingDataSourceOptions>) {
    super(instanceSettings);
  }

  getDefaultQuery(_: CoreApp): Partial<StreamingQuery> {
    return DEFAULT_QUERY
  }
}
