import { DataQuery, DataSourceJsonData } from '@grafana/data';

export interface StreamingQuery extends DataQuery {
  topic: string;
  useSchemaRegistry: boolean;
}

export const DEFAULT_QUERY: Partial<StreamingQuery> = {
  topic: "basysafe.initiator.status",
  useSchemaRegistry: true,
};

/**
 * These are options configured for each DataSource instance
 */
export interface StreamingDataSourceOptions extends DataSourceJsonData {
  kafkaUrl?: string;
  schemaRegistryUrl?: string
}


